import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router";
import isEmpty from "./isEmpty";
import { validateForm } from "./validateJS";
import emailjs from "@emailjs/browser";
import db from "./firebase";
import { collection, getDocs, deleteDoc, doc } from "firebase/firestore";

function OrderForm({ promoCode }) {
    const [formInputs, setFormInputs] = useState({
        fullName: "",
        phoneNumber: "",
        email: "",
        course: "Python",
        promo: "",
    });
    const [formErrors, setFormErrors] = useState(null);
    const [discountAmount, setDiscountAmount] = useState(200);
    const navigate = useNavigate();

    const handleChange = (e) => {
        const value = e.target.value;
        const name = e.target.name;
        setFormInputs({ ...formInputs, [name]: value });
    };

    useEffect(() => {
        setFormErrors(validateForm(formInputs));
    }, [formInputs]);

    const onSubmit = (e) => {
        e.preventDefault();
        console.log("EMAIL SEND");
        const message = `
        Full Name: ${formInputs.fullName}
        Phone number: +1${formInputs.phoneNumber}
        Email: ${formInputs.email}
        Course: ${formInputs.course}
        Promo: ${formInputs?.promo}
        ${discountAmount !== 200 && `Discount: ${discountAmount}`}
        ${promoCode !== 200 && `URL PROMO: ${promoCode}`}
        `;

        emailjs
            .send(
                "service_fju1svr",
                "template_85f6i4d",
                { fullName: formInputs.fullName, message: message },
                "hhBU5Eb9llm0Pv9nX"
            )
            .then(
                (result) => {
                    console.log(result.text);
                    navigate("/success");
                },
                (error) => {
                    console.log("ERROR", error.text);
                    e.reset();
                }
            );

        deleteDoc(doc(db, "promoCodes", promoCode));
    };

    const phoneNumberInputChange = (e) => {
        let phoneNumber = e.target.value.replace(/\D/g, ""); // Remove non-numeric characters
        const phoneNumberLength = phoneNumber.length;

        if (phoneNumberLength > 10) {
            phoneNumber = phoneNumber.slice(0, 10);
        }

        if (phoneNumberLength > 0) {
            // Format the phone number with dashes based on its length
            if (phoneNumberLength < 4) {
                phoneNumber = phoneNumber.replace(/^(\d{0,3})/, "$1");
            } else if (phoneNumberLength < 7) {
                phoneNumber = phoneNumber.replace(
                    /^(\d{0,3})(\d{0,3})/,
                    "$1-$2"
                );
            } else {
                phoneNumber = phoneNumber.replace(
                    /^(\d{0,3})(\d{0,3})(.*)/,
                    "$1-$2-$3"
                );
            }
        }

        setFormInputs({ ...formInputs, [e.target.name]: phoneNumber });
    };

    const fetchPromoCodes = async () => {
        try {
            const querySnapshot = await getDocs(collection(db, "promoCodes"));
            const fetchedPromoCodes = [];
            querySnapshot.forEach((doc) => {
                fetchedPromoCodes.push({ id: doc.id, ...doc.data() });
            });

            const foundPromoCode = fetchedPromoCodes.find(
                (promo) => promo?.id === promoCode
            );
            if (foundPromoCode) {
                setDiscountAmount(foundPromoCode?.discount);
            } else {
                setDiscountAmount(200);
            }
        } catch (error) {
            console.error("Error fetching promo codes: ", error);
        }
    };

    useEffect(() => {
        if (promoCode) {
            setFormInputs({ ...formInputs, promo: promoCode });
            fetchPromoCodes();
        }
    }, [promoCode]);

    return (
        <div className="container my-5 pb-5">
            <div
                className="text-center display-6 fw-medium text-darking pt-5"
                id="register"
            >
                Register now
            </div>
            <div className="row justify-content-center mt-5">
                <div className="col-11 col-md-6 shadow py-3 px-4">
                    <form onSubmit={onSubmit}>
                        <div class="mb-3">
                            <label for="fullName" class="form-label">
                                Your Full Name
                            </label>
                            <input
                                type="text"
                                class="form-control placeholder-sm nodefault border fw-light rounded-1"
                                id="fullName"
                                value={formInputs?.fullName}
                                name="fullName"
                                onChange={(e) => handleChange(e)}
                                placeholder="Ex: John Wick"
                            />
                        </div>
                        <div class="mb-3">
                            <label for="email" class="form-label">
                                Your Email
                            </label>
                            <input
                                type="email"
                                class="form-control placeholder-sm nodefault border fw-light rounded-1"
                                id="email"
                                placeholder="example@business.com"
                                value={formInputs?.email}
                                name="email"
                                onChange={(e) => handleChange(e)}
                            />
                        </div>
                        <div class="mb-3">
                            <label for="phoneNumber" class="form-label">
                                Your Phone Number
                            </label>
                            <div class="input-group mb-3">
                                <span
                                    class="input-group-text"
                                    id="basic-addon1"
                                >
                                    +1
                                </span>
                                <input
                                    id="phoneNumber"
                                    type="text"
                                    class="form-control placeholder-sm nodefault border fw-light rounded-1"
                                    placeholder="2534864162"
                                    aria-label="phoneNumber"
                                    aria-describedby="basic-addon1"
                                    value={formInputs?.phoneNumber}
                                    name="phoneNumber"
                                    onChange={(e) => phoneNumberInputChange(e)}
                                />
                            </div>
                        </div>
                        <div className="mb-3">
                            <label for="course" class="form-label">
                                Course
                            </label>
                            <select
                                class="form-select fw-light"
                                id="course"
                                aria-label="Default select example"
                                value={formInputs?.course}
                                name="course"
                                onChange={(e) => handleChange(e)}
                            >
                                <option value="Python">Python</option>
                                <option value="Frontend">Frontend</option>
                                <option value="Javascript-React">
                                    Javascript-React
                                </option>
                                <option value="Backend">Backend</option>
                                <option value="Mobile">Mobile</option>
                            </select>
                        </div>

                        <div class="mb-3">
                            <label for="promoCode" class="form-label">
                                Promo code{" "}
                                <span className="fw-light small">
                                    (optional)
                                </span>
                            </label>
                            <input
                                type="text"
                                class="form-control placeholder-sm nodefault border fw-light rounded-1"
                                id="promoCode"
                                placeholder="Ex: STARTER"
                                value={formInputs?.promo}
                                name="promo"
                                onChange={(e) => handleChange(e)}
                            />
                            {promoCode && (
                                <div
                                    id="emailHelp"
                                    class="form-text text-center mt-2 "
                                >
                                    {discountAmount === 200 ? (
                                        <span className="text-danger">
                                            Sorry, your promo code is not valid!
                                        </span>
                                    ) : (
                                        <span className="text-success">
                                            You won {discountAmount}% discount!
                                            Congratulations!!!
                                        </span>
                                    )}
                                </div>
                            )}
                        </div>

                        <div className="d-flex justify-content-center">
                            <button
                                type="submit"
                                class="btn-main py-2 px-5 ms-0"
                                disabled={!isEmpty(formErrors)}
                            >
                                Submit
                            </button>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    );
}

export default OrderForm;
