import React, { useState, useRef } from "react";
import courseDB from "./data";

function CourseChooseModal({ chosenCourse, setChosenCourse }) {
    const [course, setCourse] = useState(
        chosenCourse ? chosenCourse : "Python"
    );
    const closeBTNRef = useRef();

    const submitInfo = () => {
        setChosenCourse(course);
        closeBTNRef.current.click();
    };
    return (
        <div
            class="modal fade"
            id="courseModal"
            tabindex="-1"
            aria-labelledby="exampleModalLabel"
            aria-hidden="true"
        >
            <div class="modal-dialog modal-dialog-centered modal-dialog-scrollable">
                <div class="modal-content border-0">
                    <div class="px-3 pt-3 pb-2  border-bottom">
                        <button
                            type="button"
                            class="btn   shadow-none p-0 pe-4"
                            data-bs-dismiss="modal"
                            aria-label="Close"
                            ref={closeBTNRef}
                        >
                            <i class="bi bi-x text-darking fs-5"></i>
                        </button>
                        <span className="h6 fw-bold ps-5">
                            Tell us about yourself
                        </span>
                    </div>
                    <div class="modal-body px-4">
                        <div className="h6 text-darking mb-3">Course</div>
                        <div className="d-flex align-items-start flex-wrap">
                            {courseDB &&
                                courseDB.map((item) => (
                                    <div
                                        key={item.name}
                                        className={`fw-light me-2 mb-2 btn-outline ${
                                            item.name === course && "active"
                                        }`}
                                        onClick={() => setCourse(item.name)}
                                    >
                                        {item.name}
                                    </div>
                                ))}
                        </div>

                        <hr />
                        <div className="py-1">
                            <div className="row align-items-center">
                                <div className="col-6 h6 text-darking ">
                                    Course Duration
                                </div>

                                <div className="col-6 fw-light  btn-outline text-center">
                                    Available only: online
                                </div>
                            </div>
                        </div>
                        <hr />
                        <div className="py-1">
                            <div className="row align-items-center">
                                <div className="col-6 h6 text-darking ">
                                    Course type
                                </div>

                                <div className="col-6 fw-light  btn-outline text-center">
                                    Available only: individual
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="modal-footer d-flex justify-content-center">
                        <button
                            className="btn-black w-100 fs-5 fw-medium"
                            onClick={submitInfo}
                        >
                            Update course details
                        </button>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default CourseChooseModal;
